<template lang="html">
  <div class="_full_router chargeBody">
    <custHeader :title="title1"></custHeader>

    <div class="card" @click="choseCard">
      <span style="float: right;margin-top: 12px;margin-right: 20px;">{{ bankTitle }}</span>
      <div style="padding-top: 66px;text-align: right; padding-right: 22px;">{{ bankAcct | bankcard}}</div>
    </div>

    <mt-cell title="可取金额" :value="fundWithDraw" class="takeMoney"></mt-cell>
    <mt-field label="提现金额" placeholder="请输入提现金额" v-model="bizAmt"  type="number"></mt-field>
    <mt-field label="交易密码" placeholder="请输入交易密码" v-model="fundPass"  type="password"></mt-field>
    <mt-field label="验证码" placeholder="请输入验证码" v-model="captcha"><mt-button size="small" type="primary" :disabled=idDisabled @click="send">{{ capMessage }}</mt-button></mt-field>
    <mt-button  type="primary" size="large"  @click="takeMoney" style="margin-top: 10px;">提现</mt-button>


    <mt-popup
      v-model="popupVis"
      position="right"
      style="width:100%"
      >
      <div class="choseCardBody">
        <mt-header :title="title2" class="cust-header" style="background:#ef4f4f" ><mt-button icon="back" @click="back" slot="left">返回</mt-button></mt-header>
        <ul style="margin-top:10px;">
          <li class="cards" v-for="(item,index) in bankCards" @click="chose(item.bankTitle,item.bankAcct,item.bankCode)">
            <div style="text-align: right;margin-top: 12px;margin-right: 20px;height:20px;margin-bottom:10px">{{ item.bankTitle }}</div>
            <div style="text-align: right; padding-right: 22px;height:50px;">{{ item.bankAcct }}</div>
          </li>
          <li class="cards"><a href="#/personal/bankCardsAdd">+添加银行卡</a></li>
        </ul>
      </div>

    </mt-popup>
  </div>
</template>

<script>
import {MessageBox} from 'mint-ui'
import custHeader from  'components/custHeader'
export default {
  data(){
    return {
      title1:'提现',
      title2:'选择银行卡',
      bankTitle:'',
      bankCode:'',
      bankAcct:'',
      fundPass:'',
      captcha:'',
      capMessage:'发送短信',
      idDisabled:false,
      bankCards:[],
      bizAmt:'',
      fundWithDraw:'',
      popupVis:false,
      rules:{
        mb:{
          str:"required,mobile",
          state:''
        }
      }
    }
  },
  created(){
    var self = this;
    axiosUtil.qryCustSumAsset().then(data => {
      var data = data.data;
      if(data.errorcode==0){
          var data=data.data;
          self.fundWithDraw = data.fundWithDraw + ".00"
      }else{
          MessageBox("提示",data.errormsg);
      }
    })

    axiosUtil.upCltFundAppQry().then(function(data){

      var data = data.data.data
      for (var i = 0; i < data.length; i++) {
        self.bankCards.push({bankCode:data[i].bankCode,bankAcct:data[i].bankAcct,bankTitle:data[i].orgName})
        // self.bankCards.push({bankCode:data[i].bankCode,bankAcct:data[i].bankAcct})
      }

      self.bankTitle = self.bankCards[0].bankTitle
      self.bankCode = self.bankCards[0].bankCode
      self.bankAcct = self.bankCards[0].bankAcct

    });

  },
  methods:{
    send:function(){
      var that = this;
      if(!(this.rules.mb.state !== 'error' &&  this.rules.mb.state !== '' && this.mb)){
        MessageBox("提示", "你的手机号格式不正确");
        return false
      }
      axiosUtil.sendSms({
        askBizCode: 300009,
        mb:this.mb
      }).then(function(data){
        if (data.errorcode == 1) {
          MessageBox("提示", data.errormsg);
        } else{
          let time = 60;
          that.capMessage = time + "s后重新发送"
          that.idDisabled = true;
          let timer = setInterval(function(){
            time = time - 1;
            that.capMessage = time + "s后重新发送";
            if(!time){
              // time = 60
              that.capMessage = '发送短信'
              that.idDisabled = false;
              clearInterval(timer)
            }
          },1000)
        }
      })

    },
    takeMoney:function(){
      var self = this
      if (global.isOpenMd5) {
        this.fundPass = global.md5(this.fundPass);
      }
      axiosUtil.upCltFundOutApp({recBankCode:self.bankCode,recBankAcct:self.bankAcct,askBizAmt:self.bizAmt,password:self.fundPass}).then(function(data){
        var data = data.data;
        if(data.errorcode != 0){
          MessageBox("提示", data.errormsg);
        }else{
          MessageBox.alert("您申请的业务已受理，请耐心等待！").then(action => {
            router.push("/personal")
          })
        }
      })
    },
    choseCard:function(){
      this.popupVis = true
    },
    back:function(){
      this.popupVis = false
    },
    chose:function(title,acct,code){
      this.popupVis = false
      this.bankTitle = title
      this.bankAcct = acct
      this.bankCode = code
    }
  },
  components:{
     custHeader
  }
}
</script>

<style lang="css" scoped>
.chargeBody{
  background-color: #eee;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 4;
}

.card{
  height: 100px;
  width: 95%;
  margin: 10px;
  background-color: #fff;
  border-radius: 7px;
}
.cards{
  height: 114px;
  text-align: center;
  line-height: 114px;
  background-color: #fff;
  border-radius: 8px;
  margin: 10px;
  font-size: 22px;
}

.choseCardBody{
  background-color: #eee;
  height: 178vmin;
}






</style>



